
'use server';

export interface AqiData {
    uaqi: number;
    category: string;
    dominantPollutant: string;
}

export async function lookupAirQuality(
  latitude: number,
  longitude: number
): Promise<{ success: boolean; data?: AqiData, error?: string; }> {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;
  if (!apiKey) {
    return { success: false, error: "Google Maps API key is not configured on the server." };
  }
  
  const url = 'https://airquality.googleapis.com/v1/currentConditions:lookup';

  const headers = {
    'Content-Type': 'application/json',
    'X-Goog-Api-Key': apiKey,
  };
  
  const body = JSON.stringify({
    location: {
      latitude,
      longitude
    },
    // Universal AQI is recommended for general purpose use
    extraComputations: ["HEALTH_RECOMMENDATIONS", "POLLUTANT_ADDITIONAL_INFO"],
    uaqiColorPalette: "INDIGO_PERSIAN_GREEN"
  });

  try {
    const response = await fetch(url, { method: 'POST', headers, body });
    const data = await response.json();

    if (data.error || !data.indexes) {
      return { success: false, error: `Air Quality API error: ${data.error?.message || 'No data returned'}` };
    }
    
    // Find the Universal AQI
    const uaqiData = data.indexes.find((idx: any) => idx.code === 'uaqi');
    
    if (!uaqiData) {
        return { success: false, error: 'Universal AQI data not found in response.' };
    }

    const result: AqiData = {
        uaqi: uaqiData.aqi,
        category: uaqiData.category,
        dominantPollutant: uaqiData.dominantPollutant,
    };
    
    return { success: true, data: result };
  } catch (e) {
    console.error("Error fetching Air Quality data:", e);
    return { success: false, error: 'An unknown error occurred while fetching Air Quality data.' };
  }
}
