'use client';

import React, { useState, useEffect, useMemo } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { MapComponent } from '@/components/map';
import { useToast } from '@/hooks/use-toast';
import { LoaderCircle, MapPin, LocateFixed, Wind, AlertTriangle } from 'lucide-react';
import { lookupAirQuality, AqiData } from '@/lib/air-quality-service';
import { Badge } from '@/components/ui/badge';

const getAqiInfo = (aqi: number) => {
    if (aqi <= 50) return { level: 'Good', className: 'bg-green-500', suggestion: 'Air quality is considered satisfactory, and air pollution poses little or no risk.' };
    if (aqi <= 100) return { level: 'Moderate', className: 'bg-yellow-500', suggestion: 'Some pollutants may be a moderate health concern for a very small number of people who are unusually sensitive to air pollution.' };
    if (aqi <= 150) return { level: 'Unhealthy for Sensitive Groups', className: 'bg-orange-500', suggestion: 'Members of sensitive groups may experience health effects. The general public is not likely to be affected.' };
    if (aqi <= 200) return { level: 'Unhealthy', className: 'bg-red-500', suggestion: 'Everyone may begin to experience health effects; members of sensitive groups may experience more serious health effects.' };
    if (aqi <= 300) return { level: 'Very Unhealthy', className: 'bg-purple-700 text-white', suggestion: 'Health alert: everyone may experience more serious health effects.' };
    return { level: 'Hazardous', className: 'bg-maroon-700 text-white', suggestion: 'Health warnings of emergency conditions. The entire population is more likely to be affected.' };
};


export default function AirQualityPage() {
  const [location, setLocation] = useState<{ latitude: number; longitude: number } | null>(null);
  const [aqiData, setAqiData] = useState<AqiData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isLocating, setIsLocating] = useState(true);
  const [mapZoom, setMapZoom] = useState(13);

  const { toast } = useToast();

  const getLocation = () => {
    setIsLocating(true);
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          });
          setIsLocating(false);
        },
        (error) => {
          console.error("Error getting location:", error);
          toast({ variant: 'destructive', title: 'Could not get your location.' });
          setIsLocating(false);
        }
      );
    } else {
        setIsLocating(false);
        toast({ variant: 'destructive', title: 'Geolocation is not supported.' });
    }
  }

  useEffect(() => {
    getLocation();
  }, []);

  useEffect(() => {
    if (location) {
      const fetchAqi = async () => {
        setIsLoading(true);
        const result = await lookupAirQuality(location.latitude, location.longitude);
        if (result.success && result.data) {
          setAqiData(result.data);
        } else {
          toast({
            variant: 'destructive',
            title: 'Failed to load Air Quality data',
            description: result.error || 'Could not fetch AQI data from the server.',
          });
        }
        setIsLoading(false);
      };
      fetchAqi();
    }
  }, [location, toast]);
  
  const userLocationPin = useMemo(() => {
    if (!location) return [];
    return [{
        id: 'user-location',
        location: { latitude: location.latitude, longitude: location.longitude },
        category: 'Home',
        severity: 'Low',
        summary: 'Your Location',
        timestamp: new Date().toISOString()
    }]
  }, [location]);

  const aqiInfo = useMemo(() => aqiData ? getAqiInfo(aqiData.uaqi) : null, [aqiData]);

  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-8">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Air Quality Index</h1>
          <p className="mt-4 text-lg text-muted-foreground">
            Real-time air quality heatmaps and alerts for your area.
          </p>
        </header>
        
        <Card className="mb-8">
            <CardHeader>
                <CardTitle className="flex items-center gap-2">
                    <MapPin className="w-5 h-5 text-primary" />
                    Your Location
                </CardTitle>
                <CardDescription>
                    Air quality data is shown for a 3km radius around this point.
                </CardDescription>
            </CardHeader>
            <CardContent>
                {isLocating ? (
                     <div className='flex items-center gap-2 text-muted-foreground'>
                        <LoaderCircle className="w-5 h-5 animate-spin" />
                        <p>Getting your current location...</p>
                    </div>
                ) : location ? (
                    <div className="flex items-center justify-between">
                        <p className='text-muted-foreground text-sm font-mono'>
                           Lat: {location.latitude.toFixed(5)}, Lng: {location.longitude.toFixed(5)}
                       </p>
                       <Button variant="outline" size="sm" onClick={getLocation}>
                            <LocateFixed className="w-4 h-4 mr-2" />
                            Refresh Location
                       </Button>
                    </div>
                ) : (
                    <p className='text-destructive'>Location could not be determined.</p>
                )}
            </CardContent>
        </Card>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <div className="space-y-6">
            <Card>
                <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                        <Wind className="w-5 h-5 text-primary" />
                        Current Air Quality
                    </CardTitle>
                </CardHeader>
                <CardContent>
                    {isLoading ? (
                         <div className='flex items-center gap-2 text-muted-foreground justify-center py-8'>
                            <LoaderCircle className='w-5 h-5 animate-spin' />
                            <p>Fetching AQI data...</p>
                        </div>
                    ) : aqiData && aqiInfo ? (
                        <div className="text-center">
                            <div className={`w-32 h-32 rounded-full mx-auto flex flex-col items-center justify-center text-white ${aqiInfo.className}`}>
                                <span className="text-4xl font-bold">{aqiData.uaqi}</span>
                                <span className="text-sm font-medium">UAQI</span>
                            </div>
                            <h3 className="text-2xl font-bold mt-4">{aqiInfo.level}</h3>
                            <p className="text-muted-foreground mt-2">{aqiInfo.suggestion}</p>
                             {aqiData.dominantPollutant && <p className="text-sm text-muted-foreground mt-4">Dominant Pollutant: <span className="font-bold">{aqiData.dominantPollutant}</span></p>}
                            {(aqiInfo.level.includes('Unhealthy') || aqiInfo.level.includes('Hazardous')) && (
                                <div className="mt-4 p-3 bg-red-50 border border-red-200 rounded-md text-red-800 flex items-center justify-center gap-2">
                                    <AlertTriangle className="w-5 h-5" />
                                    <span className="font-bold">Danger:</span> Air quality is poor.
                                </div>
                            )}
                        </div>
                    ) : (
                        <p className="text-muted-foreground text-center py-4">
                            Could not load Air Quality data.
                        </p>
                    )}
                </CardContent>
            </Card>
          </div>
          <div className="h-[600px]">
             {location && (
                <MapComponent
                    incidents={userLocationPin}
                    onPinClick={()=>{}}
                    center={{ lat: location.latitude, lng: location.longitude }}
                    zoom={mapZoom}
                    aqiHeatmap
                />
             )}
          </div>
        </div>
      </main>
    </div>
  );
}
